<template>
  <div class="content">
    <!--header-->
    <Header>
      <div slot="right" v-if="showEditDelete" @click="deleteEdit">删除</div>
    </Header>
    <div class="newBlock">
      <div class="wrap">
        <ul class="block-ul">
          <li>
            <div>收货人姓名</div>
            <div><input type="text" placeholder="请填写收货人姓名" class="uni-input" v-model="data.resData.co" /></div>
          </li>
          <li>
            <div>手机号码</div>
            <div><input type="text" placeholder="请填写收货人手机号码" class="uni-input" maxlength="11" v-model="data.resData.mo" /></div>
          </li>
          <li>
            <div>所在地区</div>
            <div @click="addressSelect">
              <input type="text" placeholder="省市县、乡镇等" v-model="data.resData.rn" readonly="readonly" />
            </div>
            <div class="block-position" @click="addressSelect">
              <img alt :src="`${$cdnImageUrl}/search/position.png` | imageToHttps" />
              <span>一键定位</span>
            </div>
          </li>
          <li>
            <div>详细地址</div>
            <div>
              <textarea
                class="detail-textarea"
                v-model="data.resData.ar"
                placeholder="街道、楼牌号等"
                maxlength="100"
                :show-count="data.showCount"
              />
            </div>
          </li>
        </ul>
        <div class="ctrlBox" v-if="!isSubmitGameAddress">
          <div v-if="showCtrl" class="ctrlContent">
            <textarea
              id="addressContent"
              v-model="distinguishText"
              class="ctrlTest"
              maxlength="120"
              placeholder="试试粘贴收件人姓名、手机号、收货地址，可快速识别您的收货信息"
            ></textarea>
            <div class="divbox" v-if="showDivBox">
              <div class="closedistinguish" @click="closeDistinguish">清除</div>
              <div class="distinguish" @click="distinguish">确定</div>
            </div>
          </div>
          <div class="ctrlBtn" @click="ctrlBtns">
            地址粘贴板
            <img v-if="showCtrl" :src="`${$cdnImageUrl}/home/addressEdit_top.png`" alt="" />
            <img v-else :src="`${$cdnImageUrl}/home/addressEdit_down.png`" alt="" />
          </div>
        </div>
        <div v-if="!isSubmitGameAddress" class="labels">
          <div class="lables-tab">标签</div>
          <div class="labelBoxUl">
            <ul>
              <li
                :class="{ active: tableIndex == item.id }"
                v-for="(item, index) in labelsList"
                :key="index"
                @click="tableIndexs(item.id, item.name)"
              >
                {{ item.name }}
              </li>
            </ul>
            <div class="addMore" @click="addMore" v-show="showAdd">+</div>
            <div class="addOk" v-show="showAddOk">
              <input type="text" class="uni-input" ref="inputs" v-model="addtext" placeholder="请输入标签名称，最多5个字" />
              <span :class="{ AddOkClass: showAddOkClass }" @click="addOkTest">确定</span>
            </div>
            <div class="addEndit" v-show="addEndit">
              <div :class="{ 'addEndit-text': true, enditActive: showEnditActive }" @click="addEnditActive">{{ addtext }}</div>
              <div :class="{ 'addEndit-endit': true, 'enditActive-back': showEnditActive }" @click="addEnditModity">编辑</div>
            </div>
          </div>
        </div>
        <div v-if="!isSubmitGameAddress" class="line">
          <div>
            <span>设置默认地址</span>
            <span>提醒:每次下单会默认推荐使用该地址</span>
          </div>
          <van-switch inactive-color="#eeeeee" style="transform:scale(0.6)" @change="switchChange" v-model="data.deChecked" />
        </div>
      </div>
      <div class="bottomPreservation">
        <div @click="editSubmit" class="create-btn">
          保存
        </div>
      </div>
    </div>
    <!-- <div class="block">
      <div class="line">
        <input class="uni-input" v-model="data.resData.co" placeholder="收货人" />
      </div>
      <div class="line">
        <input class="uni-input" v-model="data.resData.mo" placeholder="电话号码" />
      </div>
      <div class="line" @click="addressSelect">
        <div>{{data.resData.rn ? data.resData.rn : '所在地区'}}</div>
        <span class="icon-right-arrow"></span>
      </div> -->
    <!-- <div class="line">
        <div>街道</div>
        <img class="right-arrow" src="data:image/png;base64," />
      </div> -->
    <!-- <div class="line">
        <textarea class="detail-textarea" v-model="data.resData.ar" placeholder="详细地址" maxlength="100" :show-count="data.showCount"/>
      </div>
      <div class="line" v-if="!isSubmitGameAddress">
        <div>设置默认地址</div>
        <van-switch inactive-color="#eeeeee" style="transform:scale(0.6)" @change="switchChange" v-model="data.deChecked" />
      </div>
    </div> -->
    <van-popup v-model="data.showDialog" position="bottom" @close="onHide" @click="onHide">
      <change-geographic-location ref='geoLocation' :onlyread="true"  @onHide="onHide" @handleLocation="handleLocation" />
    </van-popup>
  </div>
</template>

<script>
import { mapMutations, mapActions } from 'vuex'
import AddressParse from 'zh-address-parse'
import { LBS_KEY } from '@/config/constKeys'
/**
 * header
 */
import Header from '@/components/header'

/*
 * 接口配置文件
 */
import personalApi from '@/http/service/index'
import { gameAddAddress, queryWinnerAddress, saveWinnerAddress } from '@/http/service/game'
/*
 * 省市区级联弹窗
 */
import changeGeographicLocation from '@/components/change-geographic-location/change-geographic-location'

// import Vue from 'vue';
// import { Switch, Toast } from 'vant';
// Vue.use(Toast);
// Vue.use(Switch)

/*
 * 个人中心 编辑收货地址
 */
// const options = {
//   type: 0, // 哪种方式解析，0：正则，1：树查找
//   textFilter: [], // 预清洗的字段
//   nameMaxLength: 4, // 查找最大的中文名字长度
// }
// // type参数0表示使用正则解析，1表示采用树查找, textFilter地址预清洗过滤字段。
// const parseResult = AddressParse('addressContent', options)
export default {
  name: 'addressEdit',
  components: {
    changeGeographicLocation,
    Header,
  },
  data() {
    return {
      /**
       * {id} 收货地址id 有id是编辑收货地址，没有是新建收货地址
       * {type} 0创建收货地址 1编辑收货地址
       * {resData} 接口返回的地址信息
       * {ar} 详细地址
       * {ci} 城市id
       * {co} 收货人
       * {de} 是否是默认：1是，0否 （传参）
       * {deChecked} 是否是默认：true是，false否 （switch切换用）
       * {mo} 手机号
       * {ph} 座机（废弃）
       * {pi} 省id
       * {ri} 区id
       * {rn} 省市区地址
       * {si} 街道id
       * {zc} 邮编（废弃）
       * {showCount} 是否渲染字数统计功能（是否删除默认计数器/是否显示字数统计）
       * {showDialog} true弹窗现实 false弹窗隐藏
       */
      data: {
        id: '',
        type: '0',
        showCount: false,
        showDialog: false,
        deChecked: false,
        isSaving: false, // 是否正在保存地址
        resData: {
          ar: '',
          ci: '',
          ct: '',
          co: '',
          de: '0',
          mo: '',
          ph: '',
          pi: '',
          pt: '',
          ri: '',
          rt: '',
          rn: '',
          si: '',
          st: '',
          zc: '',
          labelId: '',
          labelName: '',
        },
        titleName: '新建收货地址',
      },
      labelsList: [], // 不包含自定义
      labelsListAll: [], // 包含自定义
      tableIndex: null,
      showAdd: true, // 显示增加标签
      showAddOk: false, // 输入标签
      showAddOkClass: false, // 给确认按钮添加颜色
      addEndit: false, // 编辑
      addtext: '', // 输入内容
      showEnditActive: false, // 是否选中状态的编辑
      showCtrl: false, // 是否展开粘贴板
      distinguishText: '',
      options: {
        type: 0, // 哪种方式解析，0：正则，1：树查找
        textFilter: [], // 预清洗的字段
        nameMaxLength: 4, // 查找最大的中文名字长度
      },
      showDivBox: false, // 是否显示粘贴板的按键
      customBable: {}, // 自定义标签
      showEditDelete: false, // 显示删除按钮
      loading: false, // 等待状态
      isSubmitGameAddress: false, // 大转盘游戏收货地址模式
      gameInfo: {},
    }
  },
  watch: {
    // 监听输入是否展示确认class
    addtext(newOld, old) {
      if (newOld) {
        this.showAddOkClass = true
      } else {
        this.showAddOkClass = false
      }
    },
    distinguishText(newOld, old) {
      if (newOld === '') {
        this.showDivBox = false
      } else {
        this.showDivBox = true
        if (newOld.length >= 120) {
          this.$toast('最多只支持120字哦~')
        }
      }
    },
    'data.resData.ar': {
      handler(newVal, oldVal) {
        if (newVal.length >= 100) {
          this.$toast('最多只支持100字哦~')
        }
      },
      deep: true,
    },
  },
  created() {
    const context = this
    const _query = context.$route.query

    /**
     * 收货地址id 有id是编辑收货地址，没有是新建收货地址
     */
    context.titleName = _query.id ? '编辑收货地址' : '新建收货地址';
    context.$route.meta.title = context.titleName
    context.showEditDelete = !!_query.id;
    context.getLabels()
    context.isSubmitGameAddress = !!_query.gameId
    context.gameInfo = _query.gameId
      ? {
        gameId: _query.gameId,
        uuid: _query.uuid,
        prizeId: _query.prizeId,
      }
      : {}
  },
  mounted() {
    const context = this
    const _query = context.$route.query
    context.loading = true
    /**
     * 收货地址id 有id是编辑收货地址，没有是新建收货地址
     */
    if (_query.id) {
      context.data.id = _query.id
      context.data.type = '1'
      /**
       * @description  个人中心 我的收获地址列表
       * @param {}
       */
      context.getAddress()
    } else if (_query.gameId) {
      context.getWinnerAddress();
    } else {
      context.loading = false
    }
  },
  methods: {
    ...mapMutations(['setLocationInfo', 'setLoaction']),
    ...mapActions(['getLatLongByLocationInfo']),
    maxNUm() {
      console.log('12')
    },
    /**
     * @description  获取标签
     * arrList 家 公司 学校
     * customBable 自定义标签
     */
    getLabels() {
      personalApi.getLabels().then(res => {
        let arrList = []
        let data = res.data.data
        this.labelsListAll = data
        if (data?.length > 3) {
          data.forEach((item, index) => {
            if (item.id === 1 || item.id === 2 || item.id === 3) {
              arrList.push(item)
            } else {
              this.customBable = item
              this.showAdd = false
              this.showAddOk = false
              this.addEndit = true
              this.addtext = item.name
            }
          })
        } else {
          arrList = data
        }
        this.labelsList = arrList
      })
    },
    /**
     * 编辑收货地址时候增加删除按钮
     */
    deleteEdit() {
      this.$dialog
        .confirm({
          title: '确定删除该地址？',
          message: '',
          // className: 'dialog-custom',
        })
        .then(() => {
          /**
           * @description 删除回调 收货地址列表 删除收货地址
           * @param {this.data.resData.id} 要删除的地址id
           * 返回接口请求数据
           */
          this.onConfirm()
        })
        .catch(() => {
          // on cancel
        })
    },
    onConfirm() {
      const context = this
      const data = {
        m: context.data.resData.id,
      }
      personalApi.deleteAddress(data).then(res => {
        const _resData = res.data
        if (_resData.success) {
          this.$toast.success('删除成功')
          context.$store.dispatch('doHistoryBack')
        } else {
          this.$toast.fail(_resData.message)
        }
      })
    },
    /**
     * 折叠粘贴板
     */
    ctrlBtns() {
      this.showCtrl = !this.showCtrl
    },
    /**
     * 清除粘贴板
     */
    closeDistinguish() {
      this.distinguishText = ''
    },
    /**
     * 识别粘贴板
     * 收货人 co  手机号码 mo 所在地区 rn 详细地址 ar
     */
    distinguish() {
      const parseResult = AddressParse(this.distinguishText, this.options)
      let arr = Object.keys(parseResult)
      this.getlbs(parseResult.city, parseResult.detail)
      if (arr.length === 0) {
        this.data.resData.co = ''
        this.data.resData.mo = ''
        this.data.resData.rn = ''
        this.data.resData.ar = ''
        this.loading = false
        this.$toast.success('抱歉，没有识别成功噢')
      } else {
        this.loading = true
        this.data.resData.co = parseResult.name
        this.data.resData.mo = parseResult.phone.substring(0, 11)
        if (parseResult.area === '') {
          this.data.resData.rn = ''
        } else {
          this.data.resData.rn = parseResult.province + ' ' + parseResult.city + ' ' + parseResult.area
        }
        // this.$toast.success('识别成功')
        console.log(parseResult.detail.length)
        if (parseResult.detail.length >= 100) {
          this.data.resData.ar = parseResult.detail.substring(0, 100);
        } else {
          this.data.resData.ar = parseResult.detail;
        }
        this.data.resData.ct = parseResult.province
        this.data.resData.pt = parseResult.city
        this.data.resData.rt = parseResult.area
        this.data.resData.st = ''
      }
    },
    getlbs(city, details) {
      const context = this;
      new Promise((resolve, reject) => {
        let data = {
          address: details,
          output: 'JSON',
          key: LBS_KEY,
          city: city,
        }
        personalApi
          .getLbs(data)
          .then(res => {
            let location = res.data.geocodes[0].location
            resolve(location)
          })
          .catch(err => {
            this.loading = false
            console.log(err)
          })
      }).then(res => {
        new Promise((resolve, reject) => {
          let lbsreData = {
            output: 'JSON',
            location: res,
            key: LBS_KEY,
            radius: '1000',
            extensions: 'all',
          }
          personalApi
            .getLbsRegeo(lbsreData)
            .then(res => {
              let address = res.data.regeocode.addressComponent
              // province:省 city:市 district:区 township: 街道
              context.data.resData.rn = address.province + ' ' + address.city + ' ' + address.district + ' ' + address.township
              resolve(address)
            })
            .catch(err => {
              this.loading = false
              console.log(err)
            })
        }).then(res => {
          let datas = {
            streetCode: res.towncode / 1000,
          }
          personalApi
            .getPcr(datas)
            .then(res => {
              if (res.data.data) {
                let allData = res.data.data
                context.data.resData.pi = allData.provinceId
                context.data.resData.pt = allData.pName
                context.data.resData.ci = allData.cityId
                context.data.resData.ct = allData.cName
                context.data.resData.ri = allData.regionid
                context.data.resData.rt = allData.rName
                context.data.resData.si = allData.streetId
                context.data.resData.st = allData.sName
                context.data.resData.rn = allData.regionName
                console.log(context.data.resData)
                context.$toast.success('识别成功')
              } else {
                context.$toast.fail('识别失败')
              }
              context.loading = false
            })
            .catch(err => {
              this.loading = false
              console.log(err)
            })
        })
      })
    },
    /**
     * 点击编辑行时是否选中
     */
    addEnditActive() {
      if (this.tableIndex !== null) {
        this.tableIndex = null
        this.showEnditActive = true
      } else {
        if (this.addEndit && this.showEnditActive) {
          this.showEnditActive = false
          this.customBable = {
            name: '',
            id: '',
          }
        } else {
          this.showEnditActive = true
        }
      }
    },
    /**
     *编辑保存的内容
     */
    addEnditModity() {
      this.showAdd = false
      this.showAddOk = true
      this.addEndit = false
      this.$nextTick(() => {
        this.$refs.inputs.focus()
      })
    },
    /**
     *输入后验证及保存
     */
    addOkTest() {
      let addtext = this.addtext.length

      if (addtext > 5) {
        // 需要加上验证
        this.$toast('最多只能输入5个字哦~')
      } else if (addtext === 0) {
        return false
      } else {
        this.loading = true
        this.showAdd = false
        this.showAddOk = false
        this.addEndit = true
        this.tableIndex = null
        this.showEnditActive = true
        let datas = {
          name: this.addtext,
          id: this.customBable.id,
        }
        // 修改和新增
        personalApi
          .getUpdateLabel(datas)
          .then(res => {
            if (res.success) {
              this.loading = false
              this.$toast('修改成功')
              this.getLabels()
            } else {
              this.$toast.fail('修改失败')
            }
          })
          .catch(err => {
            this.loading = false
            this.$toast.fail(err.message)
          })
      }
    },
    /**
     *添加新的标签
     */
    addMore() {
      this.showAdd = false
      this.showAddOk = true
      this.$nextTick(() => {
        this.$refs.inputs.focus()
      })
    },
    /**
     * 记录标签的点击
     */
    tableIndexs(ind, name) {
      if (this.tableIndex === ind) {
        this.tableIndex = null
        this.data.resData.labelId = null
        this.data.resData.labelName = null
      } else {
        this.tableIndex = ind
        this.data.resData.labelId = ind
        this.data.resData.labelName = name
        this.showEnditActive = false
      }
    },
    /**
     * @description 点击选择所在地区
     * @param
     */
    addressSelect() {
      const context = this
      context.data.showDialog = true
    },

    /**
     * @description 点击选择所在地区 取消关闭回调
     * @param
     */
    onHide() {
      const context = this
      context.data.showDialog = false
      this.$refs.geoLocation.load()
    },

    /**
     * @description 点击选择所在地区 确认关闭回调
     * @param {pi/provinceId} 省id
     * @param {ci/cityId} 城市id
     * @param {ri/districtId} 区id
     * @param {si/townshipId} 街道id
     * @param {rn/region} 省市区地址
     */
    handleLocation(data) {
      console.log(data, 'change data')

      const context = this
      context.data.showDialog = false
      context.data.resData.pi = data.provinceId
      context.data.resData.pt = data.province
      context.data.resData.ci = data.cityId
      context.data.resData.ct = data.city
      context.data.resData.ri = data.districtId
      context.data.resData.rt = data.district
      context.data.resData.si = data.townshipId
      context.data.resData.st = data.township
      context.data.resData.rn = data.region
    },

    /**
       * @description 点击选择所在地区 确认关闭回调
       * @param {pi/provinceId} 省id
       * @param {ci/cityId} 城市id
       * @param {ri/districtId} 区id
       * @param {si/townshipId} 街道id
       * @param {rn/region} 省市区地址
       */
    handleWinnerLocation(data) {
      const context = this
      context.data.resData.pi = data.provinceId
      context.data.resData.ci = data.cityId
      context.data.resData.ri = data.regionId
      context.data.resData.si = data.streetId
      context.data.resData.rn = data.regionName
      context.data.resData.co = data.consignee
      context.data.resData.mo = data.mobile
      context.data.resData.ar = data.address
    },
    /**
     * @description  是否设置为默认地址，默认是设置当前为默认地址
     * @param {e} event
     */
    switchChange(e) {
      const context = this
      context.data.resData.de = e === true ? '1' : '0'
    },
    /**
     * @description  个人中心 我的收获地址列表
     * @param {}
     * 返回接口请求数据
     */
    getWinnerAddress() {
      const context = this
      // const data = {}
      queryWinnerAddress().then(res => {
        const _resData = res.data
        if (_resData.success && _resData.data) {
          context.data.resData = _resData.data
          context.loading = false
          let textArr = _resData.data.regionName.split(' ')
          context.data.resData.pt = textArr[0]
          context.data.resData.ct = textArr[1]
          context.data.resData.rt = textArr[2]
          context.data.resData.st = textArr[3]
          context.handleWinnerLocation(_resData.data)
        } else {
          // this.$toast.fail(_resData.message)
        }
      })
    },
    /**
     * @description  个人中心 我的收获地址列表
     * @param {}
     * 返回接口请求数据
     */
    getAddress() {
      const context = this
      const data = {}
      personalApi.getAddressList(data).then(res => {
        const _resData = res.data
        if (_resData.success) {
          for (let i = 0; i < _resData.data.length; i++) {
            if (_resData.data[i].id.toString() === context.data.id) {
              context.labelsListAll.forEach((item, index) => {
                if (item.name === _resData.data[i].label) {
                  // this.$nextTick(() => {
                  context.tableIndex = item.id
                  if (item.id === 1 || item.id === 2 || item.id === 3) {
                    context.showEnditActive = false
                  } else {
                    context.showEnditActive = true
                  }
                  // })
                }
              })
              context.data.resData = _resData.data[i]
              context.loading = false
              let textArr = _resData.data[i].rn.split(' ')
              context.data.resData.pt = textArr[0]
              context.data.resData.ct = textArr[1]
              context.data.resData.rt = textArr[2]
              context.data.resData.st = textArr[3]
              if (_resData.data[i].de.toString() === '1') {
                context.data.deChecked = true
              }
              break
            }
          }
        } else {
          this.$toast.fail(_resData.message)
        }
      })
    },

    /**
     * @description  编辑保存收货地址、创建保存收货地址
     * @param {createAddress} 创建保存收货地址
     * @param {editSaveAddress} 编辑保存收货地址
     * @param {resData} 地址信息
     */
    editSubmit() {
      if (this.isSaving) return
      const context = this
      const data = context.data.resData
      // 校验收货人是否为空
      if (!data.co) {
        context.loading = false
        this.$toast.fail('请填写收货人姓名！')
        return false
      }
      // 校验手机号
      const _phone = context.data.resData.mo
      const reg = /^[1][3,4,5,6,7,8,9][0-9]{9}$/
      if (!reg.test(_phone)) {
        context.loading = false
        this.$toast.fail('请输入正确的手机号码！')
        return false
      }

      this.isSaving = true;
      this.$globalLoading.open()
      // console.log(data)
      if (data.de === '1') {
        // 设置默认地址
        this.getLatLongByLocationInfo({ townshipId: data.si, region: data.rn })
          .then(result => {
            if (result === 400) {
              this.$toast('根据位置信息获取经纬度失败')
            } else {
              let locationInfo = {
                region: data.rn,
                provinceId: data.pi,
                province: data.pt,
                cityId: data.ci,
                city: data.ct,
                districtId: data.ri,
                district: data.rt,
                townshipId: data.si,
                township: data.st,
                latitude: result.latitude,
                longitude: result.longitude,
              }
              localStorage.setItem('latitude', result.latitude)
              localStorage.setItem('longitude', result.longitude)
              localStorage.setItem('provinceId', data.pi)
              localStorage.setItem('cityId', data.ci)
              localStorage.setItem('districtId', data.ri)
              localStorage.setItem('townshipId', data.si)
              // debugger
              // localStorage.setItem('location', JSON.stringify(locationInfo))
              this.setLocationInfo(locationInfo)
              this.setLoaction({
                latitude: locationInfo.latitude,
                longitude: locationInfo.longitude,
              })
            }
            this.$globalLoading.close()
          })
          .catch(() => {
            context.loading = true
            this.$globalLoading.close()
          })
      }

      // 创建收货地址0   编辑保存收货地址1
      if (context.data.type === '0') {
        if (context.isSubmitGameAddress) {
          // 保存到游戏默认地址
          saveWinnerAddress({
            gameId: context.gameInfo.gameId,
            provinceId: data.pi,
            cityId: data.ci,
            regionId: data.ri,
            streetId: data.si,
            address: data.ar,
            mobile: data.mo,
            consignee: data.co,
            regionName: data.rn,
          }).then(res => {
            if (!context.gameInfo.uuid) {
              if (res.data.success) {
                context.$toast.success('保存成功')
                context.isSaving = false;
                context.$globalLoading.close()
                setTimeout(() => {
                  // 手动清除 Toast
                  context.$toast.clear()
                  // 返回上一层
                  context.$store.dispatch('doHistoryBack')
                }, 1000)
              } else {
                context.$toast.fail(res.data.message)
                context.isSaving = false;
                context.$globalLoading.close()
              }
            }
          })
          if (context.gameInfo.uuid) {
            // 游戏创建收获地址接口
            gameAddAddress({
              uuid: context.gameInfo.uuid + '',
              gameId: context.gameInfo.gameId,
              prizeId: context.gameInfo.prizeId,
              provinceId: data.pi,
              cityId: data.ci,
              regionId: data.ri,
              streetId: data.si,
              address: data.ar,
              mobile: data.mo,
              consignee: data.co,
              regionName: data.rn,
            })
              .then(res => {
                const _resData = res.data
                this.$globalLoading.close()
                if (_resData.success) {
                  context.$toast.success('创建成功,请留意物流信息')
                  setTimeout(() => {
                    // 手动清除 Toast
                    context.$toast.clear()
                    // 返回上一层
                    context.$store.dispatch('doHistoryBack')
                  }, 2000)
                } else {
                  context.$toast.fail(_resData.message)
                  this.isSaving = false;
                }
              })
              .catch(e => {
                this.isSaving = false;
                this.$globalLoading.close()
                context.$toast.fail(e.message)
              })
          }
        } else {
          let createData = data
          if (this.tableIndex === 1 || this.tableIndex === 2 || this.tableIndex === 3) {
            createData.label = this.data.resData.labelName
            createData.labelId = this.data.resData.labelId
          } else {
            createData.label = this.showEnditActive ? this.customBable.name : ''
            createData.labelId = this.showEnditActive ? this.customBable.id : ''
          }
          personalApi.createAddress(createData).then(res => {
            const _resData = res.data
            this.$globalLoading.close()
            if (_resData.success) {
              context.loading = false
              this.$toast.success('创建成功')
              setTimeout(() => {
                // 手动清除 Toast
                this.$toast.clear()
                // 返回上一层
                context.$store.dispatch('doHistoryBack')
              }, 1000)
            } else {
              context.loading = false
              this.$toast.fail(_resData.message)
              this.isSaving = false;
            }
          })
        }
      } else {
        let newData = data
        if (this.tableIndex === 1 || this.tableIndex === 2 || this.tableIndex === 3) {
          newData.label = this.labelsList[this.tableIndex - 1].name
          newData.labelId = this.tableIndex
        } else {
          newData.label = this.showEnditActive ? this.customBable.name : ''
          newData.labelId = this.showEnditActive ? this.customBable.id : ''
        }
        personalApi
          .editSaveAddress(newData)
          .then(res => {
            this.$globalLoading.close()
            const _resData = res.data
            if (_resData.success) {
              context.loading = false
              this.$toast.success('修改成功')
              setTimeout(() => {
                // 手动清除 Toast
                this.$toast.clear()
                // 返回上一层
                context.$store.dispatch('doHistoryBack')
              }, 1000)
            } else {
              context.loading = false
              this.$toast(_resData.message)
              this.isSaving = false;
            }
          })
          .catch(() => {
            this.isSaving = false;
            this.$globalLoading.close()
            this.loading = false
          })
      }
    },
  },
}
</script>

